<template>
  <div>
    <navber />
    <v-container>
      <v-card
        class="mx-auto"
        elevation="8"
      >
      <v-form
          ref="form"
          v-model="valid"
          lazy-validation
        >
        <v-container>
          <div class="text-center">
          <v-row>
            <v-col
              cols="12"
              sm="6"
              md="6"
            >
          <v-text-field
            prepend-icon="mdi-text"
            v-model="name"
            :counter="12"
            :rules="nameRules"
            label="รหัสติดตามสินค้า"
            filled
            required
            outlined
            dense
          ></v-text-field>
          </v-col>
          <v-col
          cols="12"
          sm="6"
          md="6"
        >
          <v-menu
            v-model="menu"
            :close-on-content-click="false"
            :nudge-right="40"
            transition="scale-transition"
            offset-y
            min-width="auto"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="date"
                label="วันที่ต้องการค้นหา"
                prepend-icon="mdi-calendar"
                readonly
                v-bind="attrs"
                v-on="on"
                filled
                outlined
                dense
              ></v-text-field>
            </template>
            <v-date-picker
              v-model="date"
              @input="menu = false"
            ></v-date-picker>
          </v-menu>
        </v-col>
        <v-spacer></v-spacer>
        </v-row>

          <v-btn
            :disabled="!valid"
            color=#693c58
            class="mr-4 mt-3 white--text"
            :loading="loading"
            @click="loading = !loading"
          >
          Search
          </v-btn>

          <v-btn
            color=#8a2121
            class="mr-4 mt-3 white--text"
            @click="reset"
          >
          Clear
          </v-btn>
        </div>
        </v-container>
        </v-form>
      </v-card>
      <v-card
        class="mx-auto mt-4"
        elevation="8"
      >
        <div class="table-stlye  th-first th-last">
          <v-data-table
            :headers="headers"
            :items="rows"
            :items-per-page="itemsPerPage"
            class="pa-5"
          >
            <template v-slot:[`item.created_at`]="{ item }">
              {{ item.created_at || '-' }}
            </template>
          </v-data-table>
        </div>
  </v-card>
  </v-container>
  </div>
</template>

<script>
import axios from 'axios'
import navber from '../lavouts/navber.vue'

export default {
  components: {
    navber
  },
  data () {
    return {
      itemsPerPage: 10,
      headers: [
        {
          text: 'วันที่',
          align: 'center',
          sortable: false,
          value: 'create_order',
          class: 'black--text'
        },
        {
          text: 'รหัสสินค้า',
          align: 'center',
          sortable: false,
          value: 'product_code',
          class: 'black--text'
        },
        {
          text: 'จำนวนสินค้า',
          align: 'center',
          sortable: false,
          value: 'quantity',
          class: 'black--text'
        },
        {
          text: 'ชื่อ-สกุล',
          align: 'center',
          sortable: false,
          value: 'des_name',
          class: 'black--text'
        },
        {
          text: 'เลขอ้างอิง',
          align: 'center',
          sortable: false,
          value: 'reference_no',
          class: 'black--text'
        },
        {
          text: 'รหัสติดตามสินค้า',
          align: 'center',
          sortable: false,
          value: 'tracking_codes',
          class: 'black--text'
        }
      ],

      rows: [],

      valid: true,
      name: '',
      nameRules: [
        v => (v && v.length <= 12) || 'รหัสติดตามสินค้าไม่เกิน 12 ตัวอักษร'
      ],

      loading: false,

      date: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
      menu: false

    }
  },
  methods: {
    getData () {
      axios.get('https://ware-house-rg-717e0dca3542.herokuapp.com/api/v1/order-in?limit=10&page=5&date_time=2023-08-26').then((resp) => {
        console.log(resp)
        this.rows = resp.data.data
      })
    },

    reset () {
      this.$refs.form.reset()
    }
  },

  mounted () {
    this.getData()
  },

  watch: {
    loading (val) {
      if (!val) return
      setTimeout(() => (this.loading = false), 2000)
    }
  }
}
</script>

<style scoped>
  .btn-round {
    border-radius: 15px;
  }
  .table-stlye >>> .v-data-table th {
    padding: 0 0px !important;
    background-color: #d59cbf !important;
    color: #513b49 !important;
  }
  .th-stlye >>> th {
    border: 1px solid #8fb2ad;
    text-align: center !important;
    border-collapse: collapse !important;
    height: 40px !important;
  }
</style>
