<template>
  <div>
    <navber />
    <v-form
          ref="form"
          v-model="valid"
          lazy-validation
        >
        <v-container>
          <div class="mt-5">
          <v-row>
          <v-col
          cols="12"
          sm="6"
          md="6"
        >
          <v-menu
            v-model="menu"
            :close-on-content-click="false"
            :nudge-right="40"
            transition="scale-transition"
            offset-y
            min-width="auto"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="date_txt"
                label="วันที่ต้องการค้นหา"
                prepend-icon="mdi-calendar"
                readonly
                v-bind="attrs"
                v-on="on"
                filled
                outlined
                dense
                :rules="[v => !!v || 'You must agree to continue!']"
              ></v-text-field>
            </template>
            <v-date-picker
              v-model="date_txt"
              @input="menu = false"
            ></v-date-picker>
          </v-menu>
        </v-col>
        <v-spacer></v-spacer>
        </v-row>

          <v-btn
            :disabled="!valid"
            color=#693c58
            class="mr-4 white--text"
            :loading="loading"
            @click="confirm()"
          >
            Search
          </v-btn>

          <v-btn
            color=#8a2121
            class="mr-4 white--text"
            @click="reset"
          >
            Clear
          </v-btn>
        </div>
        </v-container>
        </v-form>
    <v-container>
      <apexChart type="bar" height="350" :options="options" :series="series"></apexChart>
        <!--<v-sheet
          class="v-sheet--offset mx-auto mt-5"
          color="cyan"
          elevation="12"
          max-width="100%"
        >
          <v-sparkline
            :labels="val"
            :value="value"
            :label-size="labelSize"
            :show-labels="true"
            color="white"
            line-width="2"
            padding="16"
          ></v-sparkline>
        </v-sheet>-->

      <v-card
        class="mx-auto mt-4"
        elevation="8"
      >
        <div class="table-stlye  th-first th-last">
          <v-data-table
            :headers="headers"
            :items="rows"
            :items-per-page="itemsPerPage"
            class="pa-5"
          >
            <template v-slot:[`item.created_at`]="{ item }">
              {{ item.created_at || '-' }}
            </template>
          </v-data-table>
        </div>
  </v-card>
  </v-container>
  </div>
</template>

<script>
import axios from 'axios'
import navber from '../lavouts/navber.vue'

export default {
  components: {
    navber
  },
  data () {
    return {
      itemsPerPage: 1000,
      headers: [
        {
          text: 'No',
          align: 'center',
          sortable: false,
          value: 'No',
          class: 'black--text'
        },
        {
          text: 'Date',
          align: 'center',
          sortable: false,
          value: 'create_order',
          class: 'black--text'
        },
        {
          text: 'Product Code',
          align: 'center',
          sortable: false,
          value: 'product_code',
          class: 'black--text'
        },
        {
          text: 'Product Name',
          align: 'center',
          sortable: false,
          value: 'product_name',
          class: 'black--text'
        },
        {
          text: 'Quantity Total',
          align: 'center',
          sortable: false,
          value: 'quantity',
          class: 'black--text'
        }
      ],

      labels: [
        'MKLot1',
        'MKLot1',
        '6am',
        '9am',
        '12pm',
        '3pm',
        '6pm',
        '9pm'
      ],

      date_txt: '',

      val: [],

      value: [],

      labelSize: 5,
      rows: [],

      valid: true,
      name: '',
      nameRules: [
        v => (v && v.length <= 12) || 'รหัสติดตามสินค้าไม่เกิน 12 ตัวอักษร'
      ],

      loading: false,

      date: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
      menu: false,

      options: {},
      series: [{
        name: 'rose gold ',
        data: []
      }]

    }
  },
  methods: {
    getData () {
      axios.get('https://ware-house-rg-717e0dca3542.herokuapp.com/api/v1/dashboard?date_time=').then((resp) => {
        console.log(resp)
        this.rows = resp.data.data
        for (const item of this.rows) {
          this.val.push(item.product_code)
          this.value.push(item.quantity_int)
        }
        this.setData()
        console.log(this.val)
      })
    },

    confirm () {
      if (this.$refs.form.validate()) {
        console.log(this.date_txt)
        this.loading = true
        this.val = []
        this.value = []
        this.rows = []
        axios.get(`https://ware-house-rg-717e0dca3542.herokuapp.com/api/v1/dashboard?date_time=${this.date_txt}`).then((resp) => {
          console.log(resp)
          this.loading = false
          this.rows = resp.data.data
          for (const item of this.rows) {
            this.val.push(item.product_code)
            this.value.push(item.quantity_int)
          }
          this.setData()
          console.log(this.val)
        })
      }
    },

    setData () {
      this.series = [{
        name: 'rose gold ',
        data: this.value
      }]
      this.options = {
        chart: {
          toolbar: {
            show: false
          },
          fontFamily: 'Sarabun, Arial, sans-serif',
          id: 'vuechart-example',
          animations: {
            enabled: true,
            easing: 'easeinout',
            speed: 700,
            animateGradually: {
              enabled: true,
              delay: 100
            },
            dynamicAnimation: {
              enabled: true,
              speed: 700
            }
          }
        },
        yaxis: {
          forceNiceScale: true,
          labels: {
            show: true,
            rotateAlways: false,
            style: {
              fontSize: '12px',
              fontFamily: 'Sarabun',
              cssClass: 'apexcharts-yaxis-label'
            },
            formatter: function (value) {
              return Number(value).toLocaleString()
            }
          }
        },
        xaxis: {
          forceNiceScale: true,
          categories: this.val, // ใช้จาก API ได้เลย
          labels: {
            show: true,
            rotate: -45,
            rotateAlways: false,
            style: {
              colors: [],
              fontSize: '10px',
              fontFamily: 'Sarabun',
              cssClass: 'apexcharts-xaxis-label'
            }
          }
        },
        plotOptions: {
          bar: {
            columnWidth: '80%',
            distributed: true,
            dataLabels: {
              position: 'top'
            }
          }
        },
        dataLabels: {
          enabled: true,
          position: 'top',
          offsetY: -25,
          style: {
            fontSize: '12px',
            colors: ['#304758']
          },
          formatter: function (value) {
            return Number(value).toLocaleString()
          }
        },
        legend: {
          show: false
        },
        fill: {
          type: 'gradient',
          gradient: {
            shade: 'light',
            type: 'horizontal',
            shadeIntensity: 0.25,
            gradientToColors: undefined,
            inverseColors: true,
            opacityFrom: 0.85,
            opacityTo: 0.85,
            stops: [50, 0, 100]
          }
        },
        grid: {
          row: {
            colors: ['#fff', '#f2f2f2']
          }
        },
        tooltip: {
          enabled: true,
          custom ({ series, seriesIndex, dataPointIndex, w }) {
            return (
              "<div style='background-color: #c7dbe0;padding:5px;'><span>" +
              w.globals.labels[dataPointIndex] +
              '</span></div>' +
              "<div style='padding: 10px;'><b>จำนวนสินค้า : " +
              series[seriesIndex][dataPointIndex] +
              '</b></div>'
            )
          }
        }
      }
    },

    reset () {
      this.$refs.form.reset()
    }
  },

  mounted () {
    this.getData()
  },

  watch: {
    loading (val) {
      if (!val) return
      setTimeout(() => (this.loading = false), 2000)
    }
  }
}
</script>

<style scoped>
  .btn-round {
    border-radius: 15px;
  }
  .table-stlye >>> .v-data-table th {
    padding: 0 0px !important;
    background-color: #d59cbf !important;
    color: #513b49 !important;
  }
  .th-stlye >>> th {
    border: 1px solid #8fb2ad;
    text-align: center !important;
    border-collapse: collapse !important;
    height: 40px !important;
  }
  .v-sheet--offset {
    top: -24px;
    position: relative;
  }
</style>
